.app {
  /* width: clamp(20em, 50%, 40em); */
  height: 100%;
  padding-left: .8em;
  display: flex;
  flex-direction: column;
}

header {
  position: sticky;
  top: 0;
  padding-block: 1em;
  display: flex;
  align-items: center;
  gap: .5em;
  backdrop-filter: blur(30px) brightness(.4);
  font-family: 'Source Serif Pro', sans-serif;
  font-weight: bold;
}

.header-logo {
  font-size: 1.3rem;
}

.theme-toggle {
  cursor: pointer;
  border: none;
  background-color: black;
  color: var(--color-dark);
}

[data-theme="dark"] .theme-toggle {
  background-color: var(--bg-color);
  color: var(--color);
}

main {
  flex: 1 1 auto;
  margin-top: 2em;
  display: flex;
  flex-direction: column;
  gap: .5em;
}

.cards-masonry {
  margin: 0;
  padding: 0;
  width: min-content;
  display: flex;
  gap: 1em;
  opacity: 0;
  transition: opacity 500ms ease;
}

.cards-column {
  display: flex;
  flex-direction: column;
  gap: 1em;
}

.card-title {
  margin-block: .5em;
}

.card-links {
  display: flex;
  gap: .5em;
}

.card-link {
  display: inline-block;
  text-decoration: none;
  padding: .5em;
  border-radius: .3em;
  background-color: gainsboro;
  color: black;
}

.card-link.primary {
  background-color: blue;
  color: white;
}

footer {
  margin-top: 3em;
  margin-bottom: 1em;
  flex-shrink: 0;
  padding-block: .5em;
  display: flex;
  justify-content: center;
  font-size: .9rem;
  font-family: var(--ff-serif);
}