@import url('http://fonts.cdnfonts.com/css/source-serif-pro');
@import url('http://fonts.cdnfonts.com/css/source-sans-pro');

:root {
  --bg-color: white;
  --color: black;

  --bg-color-dark: #030303;
  --color-dark: white;

  --ff-serif: 'Source Serif Pro', serif;
  --ff-sans: 'Source Sans Pro', serif;
}

h1,
h2,
h3 {
  font-family: 'Source Serif Pro', sans-serif;
}

html {
  background-color: var(--bg-color);
  font-size: 20px;
  font-family: var(--ff-sans);
}

html[data-theme="dark"] {
  background-color: var(--bg-color-dark);
  color: var(--color-dark);
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

.pill {
  border-radius: 20%/50%;
}

.fade-in {
  opacity: 1;
}